<template>
  <div class="workflow">
    <!-- 上半部分 -->
    <div class="workflow-top">
      <div class="workflow-left">
        <div v-for="(item, indey) in items" :key="indey">
        <el-card @click="cardClick(indey)" id="workflow-card" >   
          <div class="card-titlewrapper">
            <div
              class="card-title"
              v-for="(subitem, index) in subItems"
              :key="index"
            >
              <div class="card-label">{{ subitem.label }}</div>
              <div class="card-value">
                <el-input size="small" v-model="item[subitem.value]"></el-input>
              </div>
            </div>
          </div>
          <div class="card-work-area">
            <draggable
              class="dragArea list-group"
              :list="item.data"
              group="workflow"
              @change="log"
            >
              <div
                class="list-group-item"
                v-for="(element, index) in item.data"
                :key="element.id"
              >
                <el-image
                  @click="workFlowItemClick(element)"
                  class="workflow-img"
                  :src="require(`@/assets/img/${element.name}`)"
                ></el-image>
                <el-button
                  class="special-delete"
                  type="danger"
                  size="mini"
                  @click="removeWorkFlow(index, indey)"
                  icon="el-icon-delete"
                  circle
                ></el-button>
                <div>
                  <i class="el-icon-bottom"></i>
                </div>
              </div>
            </draggable>
          </div>
        </el-card>
         <el-button
         class="card-delete"
                  type="danger"
                  size="mini"
                  icon="el-icon-delete"
                  @click="cardDelete(indey)"
                  circle
                ></el-button>
        </div>
        <div class="more">
          <el-button
            type="info"
            icon="el-icon-plus"
            @click="addWorkFlowConfirm"
            circle
          ></el-button>
        </div>
      </div>
      <div class="workflow-right">
        <el-collapse v-model="activeNames">
          <draggable
            :list="list1"
            class="dragArea list-group"
            :clone="cloneEquipment"
            :group="{ name: 'workflow', pull: 'clone', put: false }"
          >
            <el-collapse-item
              :title="element.title"
              v-for="(element, index) in list1"
              :key="element.id"
              :name="index"
            >
              <div>
                <el-image
                  :src="require(`@/assets/img/${element.name}`)"
                ></el-image>
              </div>
            </el-collapse-item>
          </draggable>
        </el-collapse>
      </div>
    </div>
    <!-- 下半部分 -->
    <div>
      <el-card
        id="paraflow-card"
        :body-style="{
          padding: '0px',
          margin: '15px',
          display: 'flex',
          justifyContent: 'space-between',
        }"
        class="workflow-bottom"
      >
        <div class="paraflow">
          <el-badge
            v-for="(item, index) in workFlowParaItems"
            :key="index"
            :value="item.value"
          >
            <el-button @click="paraItemClick(index)" size="small">
              {{ item.name }}
            </el-button>
          </el-badge>
          <el-button
            v-if="workFlowParaItems.length < 5"
            type="info"
            icon="el-icon-plus"
            @click="addParaFlowConfirm"
            size="small"
            circle
          ></el-button>
        </div>
        <!-- 按钮操作部分 -->
        <div class="operation">
          <el-button
            v-if="isExecute"
            @click="executeWorkFlow"
            type="danger"
            icon="el-icon-close"
            class="right"
            size="small"
            circle
          ></el-button>
          <el-button
            v-else
            @click="executeWorkFlow"
            type="primary"
            icon="el-icon-caret-right"
            size="small"
            circle
          ></el-button>
        </div>
      </el-card>
    </div>
    <!-- 工作流添加确认弹框 -->
    <Dialog
      title="添加确认"
      @cancel="isAddFlowDialogDisplay = false"
      @confirm="addWorkFlow"
      description="确定要添加工作流吗？"
      :isDisplay.sync="isAddFlowDialogDisplay"
    ></Dialog>
    <!-- 添加工作流弹框 -->
    <el-dialog
      title="配置工作流"
      :visible.sync="isAddParaDialogDisplay"
      width="30%"
    >
      <div class="edit-content">
        <div class="content-left">流程名称</div>
        <div class="content-right">
          <el-input v-model="newParaItem.name"></el-input>
        </div>
      </div>
      <div class="edit-content">
        <div class="content-left">循环次数</div>
        <div class="content-right">
          <el-input-number
            :min="1"
            :max="100"
            v-model="newParaItem.value"
          ></el-input-number>
        </div>
      </div>
      <div slot="footer" class="btns">
        <el-button type="primary" @click="addParaFlow"> 确定 </el-button>
        <el-button v-if="isEdit" type="warning"> 取消 </el-button>
        <el-button v-else @click="removeParaFlow" type="danger">删除</el-button>
      </div>
    </el-dialog>
    <!-- 设备配置弹框 -->
    <el-dialog title="流程参数配置" :visible.sync="isEquipDialogDisplay">
        <div class="equip-img">
          <el-image
            :src="require(`@/assets/img/${selectEquipment.name}`)"
          ></el-image>
        </div>
        <!-- 转速 -->
        <div class="equip-para">
        <div class="edit-content">
          <div class="content-left">转速（r/min）</div>
          <div class="content-right">
            <el-input v-model="selectEquipment.speed"></el-input>
          </div>
        </div>
        <div class="edit-content">
          <div class="content-left">时间（s）</div>
          <div class="content-right">
            <el-input v-model="selectEquipment.time"></el-input>
          </div>
        </div>
        </div>
        <!-- 时间 -->
        
        <!-- 底部操作框 -->
        <div slot="footer" class="btns">
          <el-button type="primary" @click="addParaFlow"> 确定 </el-button>
          <el-button v-if="isEdit" type="warning"> 取消 </el-button>
          <el-button v-else @click="removeParaFlow" type="danger"
            >删除</el-button
          >
        </div>
    </el-dialog>
  </div>
</template>

<script>
//导入三方库
import draggable from "vuedraggable";

//导入自定义封装组件
import Dialog from "components/Dialog.vue";
export default {
  data() {
    return {
      input1: "",
      items: [
        {
          name: "流程1",
          scene: "场景1",
          data: [
            { name: "工作站.png", id: 5, speed: "700", time: "30" },
            { name: "PCR仪.png", id: 6, speed: "180", time: "20" },
            { name: "离心机.png", id: 7, speed: "200", time: "15" },
            { name: "微孔板.png", id: 8, speed: "300", time: "40" },
          ],
        },
        {
          name: "流程2",
          scene: "场景2",
          data: [],
        },
      ],
      subItems: [
        {
          label: "流程",
          value: "name",
        },
        {
          label: "场景",
          value: "scene",
        },
      ],
      list1: [
        { title: "工作站", name: "工作站.png", id: 22, isDisplay: true },
        { title: "PCR仪", name: "PCR仪.png", id: 62, isDisplay: true },
        { title: "离心机", name: "离心机.png", id: 73, isDisplay: true },
      ],
      workFlowParaItems: [
        { name: "Overflow1", value: 2 },
        { name: "Overflow2", value: 13 },
      ],
      newListItem:{
        title:"",
        name:"",
      },
      newParaItem: {
        name: "",
        value: 1,
      },
      selectEquipment: {
        id: "",
        name: "酶标仪.png",
        speed: 0,
        time: 200,
      },
      activeNames: ["1"],
      selectDeleteIdx: -1,
      isAddFlowDialogDisplay: false,
      isAddParaDialogDisplay: false,
      isEquipDialogDisplay: false,
      isExecute: false,
      isEdit: false,
    };
  },
  components: {
    draggable,
    Dialog,
  },
  mounted(){
    //事件监听
    this.$EventBus.$on('addEquip',({title}) => {
       const item = {...this.newListItem};
       item.title = title;
       item.name =`${title}.png`;
       this.list1.push(item);
    })
  },
  methods: {
    log(evt) {
      console.log(evt);
    },
    cloneEquipment({ name, id, isDisplay }) {
      console.log("clone");
      return {
        name: name,
        id: id + 100,
        isDisplay: !isDisplay,
      };
    },
    //添加新的流程确认
    addWorkFlowConfirm() {
      this.isAddFlowDialogDisplay = true;
    },
    //添加新的流程
    addWorkFlow() {
      //抽离出类
      let item = new Object();
      item.name = "";
      item.scene = "";
      item.data = [];
      this.items.push(item);
      this.isAddFlowDialogDisplay = false;
    },
    //添加新的流程配置确认
    addParaFlowConfirm() {
      this.isAddParaDialogDisplay = true;
      this.isEdit = true;
      this.newParaItem.name = "";
      this.newParaItem.value = 1;
    },
    //添加新的流程配置
    addParaFlow() {
      //抽离出类
      if (this.isEdit) {
        let item = { ...this.newParaItem };
        this.workFlowParaItems.push(item);
      } else {
        this.workFlowParaItems[this.selectDeleteIdx] = { ...this.newParaItem };
      }
      this.isAddParaDialogDisplay = false;
    },
    //点击执行按钮
    executeWorkFlow() {
      this.isExecute = true;
      setTimeout(() => {
        this.isExecute = false;
        this.$notify({
          title: "成功",
          message: "实验流程执行成功",
          type: "success",
        });
      }, 3000);
      this.$EventBus.$emit("successExecute", {});
    },
    //删除指定工作流
    removeWorkFlow(index, indey) {
      console.log(index, indey);
      this.items[indey].data.splice(index, 1);
    },
    //删除配置流
    removeParaFlow() {
      this.workFlowParaItems.splice(this.selectDeleteIdx, 1);
      this.selectDeleteIdx = -1;
      this.isAddParaDialogDisplay = false;
    },
    //配置流点击
    paraItemClick(idx) {
      this.isEdit = false;
      this.isAddParaDialogDisplay = true;
      this.selectDeleteIdx = idx;
      this.newParaItem = { ...this.workFlowParaItems[idx] };
    },
    //设备配置
    workFlowItemClick(data) {
      this.isEquipDialogDisplay = true;
      this.selectEquipment = data;
      console.log(data);
    },
    cardDelete(idx){
      this.items.splice(idx,1);
    }
  },
};
</script>

<style scoped>
.workflow-top {
  width: 100%;
  height: 600px;
  margin-bottom: 16px;
  display: flex;
  justify-content: space-between;
}
.workflow-left {
  width: 80%;
  display: flex;
  justify-content: flex-start;
}
.workflow-left .el-card {
  width: 200px;
  height: 600px;
  margin-right: 20px;
  overflow-y: auto;
}
.card-titlewrapper {
  height: 80px;
  margin-bottom: 10px;
}
.card-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}
.card-label {
  width: 80px;
  font-size: 14px;
  height: 32px;
  line-height: 32px;
}
.workflow-right {
  width: 18%;
}
.list-group-item {
  height: 100px;
  margin-bottom: 8px;
  display: block;
}
.workflow-img {
  width: 80px;
}
.more {
  height: 600px;
  position: relative;
}
.more .el-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.workflow-bottom {
  width: 80%;
  height: 60px;
}
.workflow .el-badge {
  margin-right: 16px;
}
.paraflow {
  display: flex;
  width: 70%;
}
.operation {
  display: flex;
  flex-direction: row-reverse;
  width: 20%;
}
.operation .el-button {
  margin-left: 16px;
}
.edit-content {
  display: flex;
  line-height: 40px;
  margin:0 auto;
}
.edit-content > div {
  margin-bottom: 20px;
}
.content-left {
  width: 100px;
  font-weight: bold;
}
.content-right {
  margin-left: 40px;
  width: 300px;
  text-align: left;
}
.special-delete {
  position: relative;
  top: -60%;
  width: 24px;
  height: 24px;
  padding: 5.5px;
}
.equip-para {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
#workflow-card{
  position: relative;
}
.card-delete{
  position: relative;
  left:86px;
  bottom:14px;
}
</style>